<template>
    <ItemDetail
        :previewHeight="420"
        :invertShadow="sameColors === null ? null : !sameColors"
    >
        <template v-slot:background>
            <Img
                :preview="data.item?.cover?.original"
                :original="data.item?.cover?.original"
                :skeleton="true"
                :checktone="true"
                @tone="data.tone = $event"
            />
        </template>
        <template v-slot:preview>
            <div class="title" :class="{ invert: !sameColors }" v-if="data.item?.title && data.tone">
                {{ locale.title(data.item?.title) }}
            </div>

            <div class="buttons" v-if="data.tone" >
                <IconButton :name="t('player.shuffle')"
                    :class="{ invert: !sameColors }"
                    class="tertiary size-xl dirty" icon="random"
                    :dunk="true"
                    @click="playAndShuffle"
                />

                <AudioToggle
                    :state="state"
                    :light="true"
                    class="size-l secondary dirty"
                    :class="{ invert: data.tone !== 'dark' }"
                    @play="play"
                    @pause="pause"
                />
            </div>
        </template>
        <template v-slot:toolbar>
            <template v-if="data.tone">
                <IconButton
                    icon="unlike"
                    class="tertiary size-xl"
                    :class="{ invert: !sameColors }"
                    :dunk="true"
                    @click="like"
                />

                <IconButton
                    icon="tg-stars"
                    class="tertiary size-xl"
                    :dunk="true"
                    @click="like"
                />

                <IconButton
                    icon="apps"
                    class="tertiary size-l"
                    :class="{ invert: !sameColors }"
                    @click="openKebab"
                />
            </template>
        </template>
        <template v-slot:detail>
            <Tracks
                v-if="data.item?.id"
                :skeletonCount="16"
                :showKebab="true"
                :params="{
                    dicts: data.item.id,
                    state: 'PUBLISHED'
                }"
                :sort="{
                    code: 'ratingTotal',
                    direction: 'desc'
                }"
                :options="{
                    type: 'dict',
                    id: data.item.id
                }"
            />
        </template>
    </ItemDetail>
</template>

<script setup>
    import { onMounted, onBeforeUnmount, reactive, computed } from 'vue';
    import api from 'ðŸ”—/api';
    import stores from '@/stores';
    import Img from 'ðŸ”—/components/ui/Img.vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import IconButton from 'ðŸ”—/components/ui/IconButton.vue';
    import ItemDetail from 'ðŸ”—/components/ui/ItemDetail.vue';
    import { useI18n } from 'vue-i18n';
    import Tracks from 'ðŸ”—/components/blocks/Tracks.vue';
    import AudioToggle from 'ðŸ”—/components/ui/AudioToggle.vue';

    const { t } = useI18n();

    const dict = api.dict();
    const locale = stores.locale();
    const tg = stores.tg();
    const others = stores.others();
    const player = stores.player();
    const modals = stores.modals();

    const props = defineProps({
        code: { type: String },
        type: { type: Object }
    });

    const data = reactive({
        item: null,
        tone: null
    });

    const state = computed(() => {
        if (!data.item?.id || !player.panes.default || player.panes.default._queue?.id !== data.item?.id) {
            return;
        }

        return player.panes.default._state;
    });

    const sameColors = computed(() => {
        if (data.tone === null) {
            return null;
        }

        return (others.theme !== 'dark' && data.tone !== 'dark') || (others.theme === 'dark' && data.tone === 'dark');
    });

    const playAndShuffle = async () => {
        if (state.value) {
            player.stop();
            player.queue.clear();
        }

        player.dict.play(data.item.code, props.type.code, { shuffle: true });
        return;
    }

    const pause = async () => {
        player.dict.pause(data.item.code);
    }

    const play = async () => {
        player.dict.play(data.item.code, props.type.code);
    }

    const like = (e) => {
        message.info(t('common.soon'))
    }

    const openKebab = () => {
        modals.open('dict-kebab', { quietClose: true, data: {
            type: props.type,
            code: props.code,
            item: data.item
        } })
    }


    onMounted(async () => {
        data.item = await dict[props.type.code].get(props.code, props.type.id);
    });

    onBeforeUnmount(() => {
    });
</script>


<style lang="scss" scoped>
    .title {
        font: var(--font-h3);
        color: var(--dark-500);
        font-weight: bold;
        text-align: center;
        -webkit-text-stroke: .5px var(--white-90);
        text-stroke: .5px var(--white-90);

        // &:before {
        //     content: "";
        //     width: 0px;
        //     height: 0px;
        //     position: absolute;
        //     background: var(--white-90);
        //     box-shadow: 0px 0px 37px 22px var(--white-90);
        //     border-radius: 50%;
        //     z-index: -1;
        //     pointer-events: none;
        // }

        &.invert {
            color: var(--white-500);
            -webkit-text-stroke: .5px var(--dark-90);
            text-stroke: .5px var(--dark-90);
            // &:before {
            //     background: var(--dark-90);
            //     box-shadow: 0px 0px 37px 22px var(--dark-90);
            // }
        }
    }

    .buttons {
        min-height: 38px;
        display: flex;
        flex-direction: row;
        gap: 8px;
        align-items: center;
        justify-content: center;

        button {
            // min-width: 132px;
        }
    }
</style>
